      </table>
      
      <!--PAGINATION-->
      <div class="p-3 lg:px-6 border-t border-gray-100 dark:border-slate-800">
        <BaseLevel>
          <BaseButtons>
            <BaseButton class="text-xs text-gray-700 uppercase dark:text-gray-400" label="Previous" @click="currentPage = Math.max(currentPage - 1, 0)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
              <BaseButton
                class="text-xs text-gray-700 uppercase dark:text-gray-400"
                v-for="page in pagesList.slice(Math.max(currentPage - 1, 0), Math.min(currentPage + 1, numPages))"
                :key="page"
                :active="page === currentPage"
                :label="page + 1"
                :color="page === currentPage ? 'lightDark' : 'whiteDark'"
                small
                @click="currentPage = page"
              />
              <BaseButton class="text-xs text-gray-700 uppercase dark:text-gray-400" label="Next" @click="currentPage = Math.min(currentPage + 1, numPages - 1)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
        </BaseButtons>

          <small>Page {{ currentPageHuman }} of {{ numPages }}</small>
        </BaseLevel>
      </div>
</div>
</template>
<script>
import { mdiEye, mdiTrashCan } from "@mdi/js";
import CardBoxModal from "@/components/CardBoxModal.vue";
import TableCheckboxCell from "@/components/TableCheckboxCell.vue";
import BaseLevel from "@/components/BaseLevel.vue";
import BaseButtons from "@/components/BaseButtons.vue";
import BaseButton from "@/components/BaseButton.vue";
import UserAvatar from "@/components/UserAvatar.vue";
import { fetchFilteredAssetData, formatValue } from "@/stores/supastore";
import { supabase, currentUser } from "../supabase";

export default {
  components: {
    CardBoxModal,
    TableCheckboxCell,
    BaseLevel,
    BaseButtons,
    BaseButton,
    UserAvatar,
    mdiEye,
    mdiTrashCan,
  },
  props: {
    type: {
      type: String,
      default: 'tvl'
    },
    pagesize: {
      type: Number,
      default: 5,
    }
  },
  data() {
    return {
      loading: true,
      assets: {},
      items: [],
      isModalActive: false,
      isModalDangerActive: false,
      perPage: this.pagesize,
      currentPage: 0,
      assetsPaginated: [],
    } 
  },
  async created() {
    this.assets = await fetchFilteredAssetData(this.assets, this.type);
    this.assets = this.assets['assets']
    this.updateAssetsPaginated();
  }, 
  watch: {
    currentPage() {
      this.assetsPaginated = Array.from(this.assets).slice(
        this.perPage * this.currentPage,
        this.perPage * (this.currentPage + 1)
      );
    }
  },
  computed: {
    numPages() {
      if (this.assets.length === 0) {
        return 0;
      }
      return Math.ceil(this.assets.length / this.perPage);
    },
    currentPageHuman() {
      return this.currentPage + 1;
    },
    pagesList() {
      const pagesList = [];
      for (let i = 0; i < this.numPages; i++) {
        pagesList.push(i);
      }
      return pagesList;
    },
  },
  methods: {
     formatValue(coin, metric) {
      return formatValue(coin, metric)
    },
    updateAssetsPaginated() {
    this.assetsPaginated = Array.from(this.assets).slice(
      this.perPage * this.currentPage,
      this.perPage * (this.currentPage + 1),
      this.loading = false
    );
  },
    async getLogo() {
      let { data: asset, error } = await supabase
          .from('ultimateframe')
          .select('logo, symbol')
          .limit(5000)

          console.log("assetzzz   ", asset)
          return asset
    }
  }
}
</script>
